import "../style/homepage.css";
import { ComboBox, IComboBoxOption, IComboBoxStyles } from "@fluentui/react";
import { Resizable } from "re-resizable";
import { TextField } from "@fluentui/react/lib/TextField";
import { DefaultButton } from "@fluentui/react/lib/Button";



export interface IHomePageProps {}

const HomePage = (props: IHomePageProps) => {
  const options: IComboBoxOption[] = [
    { key: "A", text: "JDVT: Jan de vos tunnel" },
    { key: "B", text: "KRN: Kruineike" },
    { key: "C", text: "KRN-HMI:Kruin - HMI" },
    { key: "D", text: " Kruin - IO" },
    { key: "E", text: "Kruin - OPC UA" },
    { key: "F", text: "Kruin - PLC" },
    { key: "G", text: "Oosterweel Linkeroever" },
  ];
  
  const testPlanOptions: IComboBoxOption[] = [
    { key: "A", text: "Eerste Iteratie" },
    { key: "B", text: "FAT" },
    { key: "C", text: "Tweede Iteratie" },
  ];
  
  const exStatusOptions: IComboBoxOption[] = [
    { key: "A", text: "Passed" },
    { key: "B", text: "Failed" },
    { key: "C", text: "Blocked" },
  ];
  
  const comboBoxStyles: Partial<IComboBoxStyles> = {
    root: { maxWidth: 300, marginRight: "0px", maxHeight: 20 },
  };
  
  const style = {
    border: "solid 1px #ddd",
    overflow: "scroll",
  };
  return (
    <div className="container-grid-homepage">
      <div className="header">
        <div className="header__block">
          <span>Test Project</span>
          <ComboBox
            defaultSelectedKey="A"
            options={options}
            styles={comboBoxStyles}
            calloutProps={{ doNotLayer: true }}
          />
        </div>
      </div>
      <div className="finder-details-container">
        <div
          style={{
            width: "100%",
            display: "flex",
          }}
        >
          <Resizable
            style={style}
            defaultSize={{
              width: "35%",
              height: "1000px",
            }}
            maxWidth="100%"
            minWidth="500"
          >
            <div className="flex-finder">
              <div className="header-cels">
                <p>Execute Tests</p>
              </div>
              <div className="header-title">
                <p>Settings</p>
              </div>

              <div className="filter-combobox-container">
                <span>Title</span>
                <ComboBox
                  defaultSelectedKey="B"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
                <span>Build to execute</span>
                <ComboBox
                  defaultSelectedKey="A"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
              </div>
              <div className="header-title">
                <p>Filters</p>
              </div>
              <div className="filter-combobox-container">
                <span>Test Case ID</span>
                <TextField placeholder="KRN-HMI-" />
                <span>Test Case Title</span>
                <TextField />
                <span>Test Suites</span>
                <ComboBox
                  defaultSelectedKey="B"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
                <span>Priority</span>
                <ComboBox
                  defaultSelectedKey="A"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
                <span>Excecution type</span>
                <ComboBox
                  defaultSelectedKey="B"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
                <span>Assigned to</span>
                <ComboBox
                  defaultSelectedKey="A"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
                <span>Bugs on Exec. Context</span>
                <TextField placeholder="BUGX, BUGY, BUGZ" />
                <span>Result</span>
                <ComboBox
                  defaultSelectedKey="A"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
                <span>On</span>
                <ComboBox
                  defaultSelectedKey="B"
                  options={testPlanOptions}
                  calloutProps={{ doNotLayer: true }}
                />
              </div>
            </div>
{/*             <div className="container-tree-view">
              <TreeView data={treeViewTemplate as any} />
            </div> */}
          </Resizable>
          <div
            style={{
              ...style,
              width: "100%",
              minWidth: "200px",
              background: "rgb(204,221,238)",
              overflow: "hide",
            }}
          >
            <div className="testexecution-details-container">
              <div className="header-cels">
                <p>Exec. Context: Test Plan FAT - Build FAT V01</p>
              </div>
              <div className="header-title">
                <p>Test Plan FAT</p>
              </div>
              <div className="header-title">
                <p>Buil FAT V01</p>
              </div>
              <div className="header-title">
                <p>Test Suite : HMI/ VisualisatieSchakelaar/</p>
              </div>
              <div className="last-execution-box">
                <p>Last execution (any build)</p>
                <div className="running-box">Not Run</div>
                <p>Not tested yet</p>
              </div>
              <div className="test-description-box">
                <p>
                  Test Case KRN-HMI-11 :: Version: 1 :: ResetNaNoodbediening
                </p>
                <p>No tester assigned</p>
              </div>
              <div className="test-execution-box">
                <div className="test-execution">
                  <div className="header-title">
                    <p>Summary</p>
                  </div>
                  <div className="header-title">
                    <p>Preconditions</p>
                  </div>
                  <p>Schakelaar lokaal scherm</p>
                  <div className="grid-title-step">
                    <p className="header-title">#</p>
                    <p className="header-title">Step actions</p>
                    <p className="header-title">Expected Results</p>
                    <p className="header-title">Execution</p>
                    <p className="header-title">Execution notes</p>
                    <p className="header-title">Execution status</p>
                  </div>
                  <div className="grid-step-action-container">
                    <p>1</p>
                    <p>Van groeps naar stappen via scherm</p>
                    <p>
                      De HMI knop om naar stappen over te schakelen moet
                      uitvoerbaar gevisualiseerd zijn
                    </p>
                    <p>Manual</p>
                    <TextField multiline resizable={false} />
                    <ComboBox
                      options={exStatusOptions}
                      styles={comboBoxStyles}
                      calloutProps={{ doNotLayer: true }}
                    />
                    <div className="select-file">
                      <span>File</span>
                      <DefaultButton text="Select File" />
                      <span>No file selected</span>
                    </div>
                  </div>
                  <div className="grid-step-action-container">
                    <p>2</p>
                    <p>Van stappen naar groeps via scherm</p>
                    <p>
                      De HMI knop om naar groeps over te schakelen moet
                      uitvoerbaar gevisualiseerd zijn
                    </p>
                    <p>Manual</p>
                    <TextField multiline resizable={false} />
                    <ComboBox
                      options={exStatusOptions}
                      styles={comboBoxStyles}
                      calloutProps={{ doNotLayer: true }}
                    />
                    <div className="select-file">
                      <span>File</span>
                      <DefaultButton text="Select File" />
                      <span>No file selected</span>
                    </div>
                  </div>
                  <div className="grid-step-action-container">
                    <p>3</p>
                    <p>Van stappen naar groeps via scherm</p>
                    <p>
                      De HMI knop om naar groeps over te schakelen moet
                      uitvoerbaar gevisualiseerd zijn
                    </p>
                    <p>Manual</p>
                    <TextField multiline resizable={false} />
                    <ComboBox
                      options={exStatusOptions}
                      styles={comboBoxStyles}
                      calloutProps={{ doNotLayer: true }}
                    />
                    <div className="select-file">
                      <span>File</span>
                      <DefaultButton text="Select File" />
                      <span>No file selected</span>
                    </div>
                  </div>
                  <div className="grid-step-action-container">
                    <p>4</p>
                    <p>Van stappen naar groeps via scherm</p>
                    <p>
                      De HMI knop om naar groeps over te schakelen moet
                      uitvoerbaar gevisualiseerd zijn
                    </p>
                    <p>Manual</p>
                    <TextField multiline resizable={false} />
                    <ComboBox
                      options={exStatusOptions}
                      styles={comboBoxStyles}
                      calloutProps={{ doNotLayer: true }}
                    />
                    <div className="select-file">
                      <span>File</span>
                      <DefaultButton text="Select File" />
                      <span>No file selected</span>
                    </div>
                  </div>
                  <div className="grid-step-action-container">
                    <p>5</p>
                    <p>Van stappen naar groeps via scherm</p>
                    <p>
                      De HMI knop om naar groeps over te schakelen moet
                      uitvoerbaar gevisualiseerd zijn
                    </p>
                    <p>Manual</p>
                    <TextField multiline resizable={false} />
                    <ComboBox
                      options={exStatusOptions}
                      styles={comboBoxStyles}
                      calloutProps={{ doNotLayer: true }}
                    />
                    <div className="select-file">
                      <span>File</span>
                      <DefaultButton text="Select File" />
                      <span>No file selected</span>
                    </div>
                  </div>
                  <p>Execution type : Manual</p>
                  <p>Estimated exec. dutration (min) :</p>
                </div>
              </div>
              <div className="note-description-box">
                <p>Notes / Description</p>
                <div className="grid-note-desc-box">
                  <TextField multiline resizable={false} rows={14} />
                  <div className="conclusion-box">
                    <p>Execution duration (min)</p>
                    <div style={{ width: "80%", marginBottom: "5px" }}>
                      <TextField multiline resizable={false} />
                    </div>
                    <DefaultButton text="Move to Next Test Case" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomePage;
